.app {
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
  font-family: 'Inter', Arial, sans-serif;
  margin: 0;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  -webkit-text-size-adjust: 100%;
